.portfolio-section {
  background-color: var(--color-black);
  padding: var(--space-xl) 0;
  min-height: 800px;
  position: relative;
  overflow: hidden;
}

.lightning-background {
  width: 100%;
  height: 100%;
  z-index: 0;
  opacity: .7;
  background: linear-gradient(125deg, #0a0a0a 0%, #0f0f0f 100%);
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.lightning-glow {
  width: 60%;
  height: 70%;
  filter: blur(80px);
  transform-origin: center;
  opacity: .7;
  z-index: 0;
  background: radial-gradient(circle, #c8553d1f 0%, #0a0a0a00 70%);
  animation: lightning-pulse 10s ease-in-out infinite alternate;
  position: absolute;
  top: -20%;
  right: -10%;
}

.lightning-glow.secondary {
  width: 50%;
  height: 60%;
  opacity: .4;
  animation-delay: 3s;
  top: 60%;
  left: -10%;
}

@keyframes lightning-pulse {
  0%, 100% {
    opacity: .5;
    transform: scale(1);
  }

  50% {
    opacity: .8;
    transform: scale(1.1);
  }
}

.grid-pattern {
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: .5;
  background-image: linear-gradient(to right, #c8553d08 1px, #0000 1px), linear-gradient(#c8553d08 1px, #0000 1px), repeating-linear-gradient(45deg, #c8553d03 0 1px, #0000 1px 10px), repeating-linear-gradient(-45deg, #c8553d03 0 1px, #0000 1px 10px);
  background-size: 40px 40px, 40px 40px, 20px 20px, 20px 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.portfolio-section .section-header {
  z-index: 2;
  text-align: center;
  margin-bottom: var(--space-lg);
  position: relative;
}

.section-subtitle {
  color: var(--color-stone);
  max-width: 600px;
  margin: var(--space-sm) auto 0;
  font-size: 1.1rem;
  font-family: var(--font-primary);
  letter-spacing: .3px;
  line-height: 1.6;
}

.portfolio-filter {
  margin-bottom: var(--space-lg);
  z-index: 2;
  will-change: opacity;
  justify-content: center;
  display: flex;
  position: relative;
}

.filter-container {
  backdrop-filter: blur(5px);
  background: #1a1a1a80;
  border: 1px solid #c8553d1a;
  border-radius: 2px;
  padding: 5px;
  display: inline-flex;
  position: relative;
  box-shadow: 0 4px 15px #0003, inset 0 0 0 1px #ffffff0d;
}

.filter-active-indicator {
  height: calc(100% - 10px);
  z-index: -1;
  opacity: .9;
  will-change: transform, width, left;
  background: #c8553dcc;
  border-radius: 2px;
  transition: all .4s cubic-bezier(.16, 1, .3, 1);
  position: absolute;
  bottom: 5px;
  box-shadow: 0 0 10px #c8553d4d, inset 0 0 4px #fff3;
}

.filter-btn {
  color: var(--color-gray-light);
  cursor: pointer;
  font-size: .95rem;
  font-weight: 500;
  font-family: var(--font-secondary);
  letter-spacing: .5px;
  text-transform: uppercase;
  z-index: 1;
  background: none;
  border: none;
  border-radius: 2px;
  padding: 10px 20px;
  transition: all .3s;
  position: relative;
}

.filter-btn:hover, .filter-btn.active {
  color: var(--color-white);
}

.portfolio-bento {
  gap: var(--space-md);
  z-index: 2;
  perspective: 1000px;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  display: grid;
  position: relative;
}

.portfolio-item {
  height: 320px;
  opacity: 0;
  perspective: 1000px;
  will-change: transform, opacity;
  border-radius: 2px;
  position: relative;
  overflow: visible;
  transform: translateY(30px);
}

.portfolio-item.animate-in {
  animation: fadeInUp .6s forwards;
}

.portfolio-item-inner {
  height: 100%;
  width: 100%;
  transform-style: preserve-3d;
  will-change: transform;
  border-radius: 2px;
  transition: all .5s cubic-bezier(.16, 1, .3, 1);
  position: relative;
  overflow: hidden;
  box-shadow: 0 10px 30px #0000004d, 0 0 0 1px #c8553d1a, inset 0 0 0 1px #ffffff0d;
}

.portfolio-img-container {
  height: 100%;
  width: 100%;
  transform-style: preserve-3d;
  position: relative;
  overflow: hidden;
}

.portfolio-img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: all .7s;
}

.portfolio-item:hover .portfolio-img {
  transform: scale(1.05);
}

.card-shine {
  z-index: 2;
  opacity: 0;
  pointer-events: none;
  background: linear-gradient(135deg, #fff0 0% 40%, #ffffff1a 50%, #fff0 60% 100%);
  transition: opacity .5s;
  position: absolute;
  inset: 0;
}

.portfolio-item:hover .card-shine {
  opacity: 1;
  animation: card-shine 1.5s forwards;
}

@keyframes card-shine {
  0% {
    transform: translateX(-100%)translateY(-100%)rotate(25deg);
  }

  100% {
    transform: translateX(100%)translateY(100%)rotate(25deg);
  }
}

.portfolio-overlay {
  width: 100%;
  height: 100%;
  padding: var(--space-md);
  opacity: 0;
  z-index: 3;
  will-change: transform, opacity;
  background: linear-gradient(to top, #000000f2 0%, #000c 50%, #0009 100%);
  flex-direction: column;
  justify-content: flex-end;
  transition: all .4s cubic-bezier(.16, 1, .3, 1);
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateZ(20px);
}

.portfolio-overlay.active {
  opacity: 1;
}

.portfolio-overlay:after {
  content: "";
  width: 100%;
  height: 3px;
  background: var(--color-terracotta);
  transform-origin: 0;
  transition: transform .5s;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
}

.portfolio-overlay.active:after {
  transform: scaleX(1);
}

.portfolio-category {
  color: var(--color-terracotta);
  text-transform: uppercase;
  letter-spacing: 1.5px;
  opacity: 0;
  font-size: .85rem;
  font-weight: 600;
  font-family: var(--font-secondary);
  margin-bottom: 5px;
  transition: all .3s .1s;
  transform: translateY(10px);
}

.portfolio-overlay.active .portfolio-category {
  opacity: 1;
  transform: translateY(0);
}

.portfolio-title {
  color: var(--color-parchment);
  opacity: 0;
  font-size: 1.4rem;
  font-weight: 700;
  font-family: var(--font-secondary);
  letter-spacing: .2px;
  border-left: 2px solid var(--color-terracotta);
  will-change: transform, opacity;
  margin-bottom: 8px;
  padding-left: 10px;
  line-height: 1.3;
  transition: all .3s .2s;
  transform: translateY(10px);
}

.portfolio-overlay.active .portfolio-title {
  opacity: 1;
  transform: translateY(0);
}

.portfolio-description {
  color: var(--color-stone);
  opacity: 0;
  will-change: transform, opacity;
  margin-bottom: 15px;
  font-size: .95rem;
  line-height: 1.5;
  transition: all .3s .3s;
  transform: translateY(10px);
}

.portfolio-overlay.active .portfolio-description {
  opacity: .9;
  transform: translateY(0);
}

.portfolio-link {
  color: var(--color-parchment);
  width: fit-content;
  border-bottom: 2px solid var(--color-terracotta);
  opacity: 0;
  font-size: .95rem;
  font-weight: 600;
  font-family: var(--font-secondary);
  letter-spacing: .5px;
  will-change: transform, opacity;
  align-items: center;
  gap: 8px;
  padding: 5px 0;
  text-decoration: none;
  transition: all .3s .4s;
  display: inline-flex;
  transform: translateY(10px);
}

.portfolio-overlay.active .portfolio-link {
  opacity: 1;
  transform: translateY(0);
}

.portfolio-link:hover {
  color: var(--color-terracotta-light);
  transition: all .3s;
  transform: translateX(5px);
}

.link-arrow {
  transition: all .3s;
}

.portfolio-link:hover .link-arrow {
  transform: translate(3px, -3px);
}

.portfolio-cta {
  text-align: center;
  margin-top: var(--space-xl);
  z-index: 2;
  opacity: 0;
  will-change: transform, opacity;
  position: relative;
  transform: translateY(20px);
}

.portfolio-cta.animate-fade-in {
  animation: fadeInUp .6s .6s forwards;
}

.btn-view-all {
  color: var(--color-parchment);
  border: 2px solid var(--color-terracotta);
  z-index: 1;
  font-size: 1rem;
  font-weight: 600;
  font-family: var(--font-secondary);
  letter-spacing: 1px;
  text-transform: uppercase;
  background: none;
  border-radius: 2px;
  align-items: center;
  gap: 10px;
  padding: 14px 28px;
  text-decoration: none;
  transition: all .4s cubic-bezier(.16, 1, .3, 1);
  display: inline-flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 5px 15px #0003, 0 0 0 1px #c8553d1a;
}

.btn-view-all:before {
  content: "";
  width: 100%;
  height: 100%;
  z-index: -1;
  background: linear-gradient(90deg, #0000, #ffffff1a, #0000);
  transition: left .7s;
  position: absolute;
  top: 0;
  left: -100%;
}

.btn-view-all:hover {
  background: #c8553de6;
  transform: translateY(-3px);
  box-shadow: 0 10px 25px #0000004d, 0 0 0 1px #c8553d4d;
}

.btn-view-all:hover:before {
  left: 100%;
}

.btn-view-all svg {
  transition: transform .3s;
}

.btn-view-all:hover svg {
  transform: translateX(3px);
}

.view-details-btn {
  color: var(--color-parchment);
  cursor: pointer;
  opacity: 0;
  width: fit-content;
  border-bottom: 1px dashed var(--color-terracotta-dark);
  font-size: .95rem;
  font-weight: 600;
  font-family: var(--font-secondary);
  align-items: center;
  gap: 8px;
  margin-top: 10px;
  padding: 5px 0;
  transition: all .3s .5s;
  display: inline-flex;
  transform: translateY(10px);
}

.portfolio-overlay.active .view-details-btn {
  opacity: 1;
  transform: translateY(0);
}

.view-details-btn:hover {
  color: var(--color-terracotta-light);
}

.details-icon {
  transition: transform .3s;
}

.view-details-btn:hover .details-icon {
  transform: scale(1.15);
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (prefers-reduced-motion: reduce) {
  .lightning-glow {
    animation: none;
  }

  .portfolio-item-inner {
    transition: none;
  }

  .card-shine {
    display: none;
  }

  .filter-active-indicator, .btn-view-all:before {
    transition: none;
  }
}

@media (max-width: 992px) {
  .portfolio-bento {
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  }
}

@media (max-width: 768px) {
  .filter-container {
    max-width: 100%;
    padding-bottom: 5px;
    overflow-x: auto;
  }

  .filter-btn {
    white-space: nowrap;
    padding: 8px 15px;
    font-size: .85rem;
  }

  .filter-active-indicator {
    display: none;
  }

  .filter-btn.active {
    background-color: var(--color-terracotta);
    box-shadow: inset 0 0 10px #0000004d;
  }
}

@media (max-width: 576px) {
  .portfolio-bento {
    grid-template-columns: 1fr;
  }

  .btn-view-all {
    padding: 12px 20px;
    font-size: .9rem;
  }

  .portfolio-title {
    font-size: 1.2rem;
  }
}

